/****************************/
/* General                  */
/****************************/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  background-color: #f4f4f4;
  color: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.main {
  padding: 20px 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/****************************/
/* Header                   */
/****************************/
.header {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
}

.navbar {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  justify-content: space-between;
  align-items: center;
}

.header .title {
  font-size: 1.5rem;
  text-decoration: none;
  color: #fff;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
  text-decoration: none;
  color: #fff;
  transition: color 0.3s ease;
}

.nav-links li:hover {
  color: #f0f0f0;
}

/****************************/
/* About                    */
/****************************/
.about {
  margin: 500px 0px;
}

/****************************/
/* Projects                 */
/****************************/
.projects {
  background-color: #fff;
  padding: 40px 0;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.project-item {
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 5px;
}

.project-item h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.project-item p {
  font-size: 1rem;
  color: #666;
}

/****************************/
/* Contact                  */
/****************************/
.contact {
  margin: 500px 0x;
}

/****************************/
/* Footer                   */
/****************************/
.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
  bottom: 0;
  width: 100%;
}